"use client";

import React from "react"

import { motion } from "framer-motion";
import { ArrowUpRight, ShoppingCart, CheckSquare, BarChart3, MessageCircle } from "lucide-react";
import { useLanguage } from "./language-context";

const projects = [
  {
    titleKey: "projectEcommerce" as const,
    descKey: "projectEcommerceDesc" as const,
    tags: ["Next.js", "TypeScript", "Stripe", "PostgreSQL"],
    link: "#",
    icon: ShoppingCart,
    accentColor: "oklch(0.75 0.12 180)",
    gradient: "from-[oklch(0.75_0.12_180_/_0.2)] to-[oklch(0.75_0.12_180_/_0.05)]",
  },
  {
    titleKey: "projectTask" as const,
    descKey: "projectTaskDesc" as const,
    tags: ["React", "Node.js", "Socket.io", "MongoDB"],
    link: "#",
    icon: CheckSquare,
    accentColor: "oklch(0.7 0.15 300)",
    gradient: "from-[oklch(0.7_0.15_300_/_0.2)] to-[oklch(0.7_0.15_300_/_0.05)]",
  },
  {
    titleKey: "projectAnalytics" as const,
    descKey: "projectAnalyticsDesc" as const,
    tags: ["React", "D3.js", "Python", "REST API"],
    link: "#",
    icon: BarChart3,
    accentColor: "oklch(0.85 0.15 90)",
    gradient: "from-[oklch(0.85_0.15_90_/_0.2)] to-[oklch(0.85_0.15_90_/_0.05)]",
  },
  {
    titleKey: "projectSocial" as const,
    descKey: "projectSocialDesc" as const,
    tags: ["Next.js", "Prisma", "WebSockets", "Redis"],
    link: "#",
    icon: MessageCircle,
    accentColor: "oklch(0.75 0.15 55)",
    gradient: "from-[oklch(0.75_0.15_55_/_0.2)] to-[oklch(0.75_0.15_55_/_0.05)]",
  },
];

const container = {
  hidden: { opacity: 0 },
  show: {
    opacity: 1,
    transition: {
      staggerChildren: 0.15,
    },
  },
};

const item = {
  hidden: { opacity: 0, y: 30 },
  show: { 
    opacity: 1, 
    y: 0,
    transition: {
      duration: 0.5,
      ease: "easeOut",
    }
  },
};

function ProjectCard({
  project,
}: {
  project: (typeof projects)[0];
}) {
  const { t } = useLanguage();
  const Icon = project.icon;
  const title = t[project.titleKey];
  const description = t[project.descKey];
  
  return (
    <motion.a
      href={project.link}
      variants={item}
      whileHover={{ y: -6, scale: 1.02 }}
      className="group block relative p-6 rounded-xl bg-card border border-border hover:border-transparent transition-all duration-300"
      style={{
        boxShadow: `0 0 0 1px transparent`,
      }}
    >
      {/* Hover glow effect */}
      <div 
        className="absolute inset-0 rounded-xl opacity-0 group-hover:opacity-100 transition-opacity duration-300 -z-10"
        style={{
          boxShadow: `0 0 40px ${project.accentColor.replace(')', ' / 0.15)')}`,
        }}
      />
      
      {/* Colored border on hover */}
      <div 
        className="absolute inset-0 rounded-xl opacity-0 group-hover:opacity-100 transition-opacity duration-300 pointer-events-none"
        style={{
          border: `1px solid ${project.accentColor.replace(')', ' / 0.5)')}`,
        }}
      />

      {/* Thumbnail with Icon */}
      <div className={`aspect-video w-full rounded-lg bg-gradient-to-br ${project.gradient} mb-4 overflow-hidden flex items-center justify-center relative`}>
        <Icon 
          className="w-12 h-12 transition-transform duration-300 group-hover:scale-110"
          style={{ color: project.accentColor }}
        />
        {/* Shimmer effect */}
        <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/5 to-transparent -translate-x-full group-hover:translate-x-full transition-transform duration-1000" />
      </div>

      {/* Content */}
      <div className="space-y-3">
        <div className="flex items-center justify-between">
          <h3 
            className="text-lg font-semibold text-foreground transition-colors duration-200"
            style={{ 
              color: undefined,
            }}
          >
            <span className="group-hover:text-[var(--hover-color)]" style={{ '--hover-color': project.accentColor } as React.CSSProperties}>
              {title}
            </span>
          </h3>
          <ArrowUpRight 
            className="w-4 h-4 text-muted-foreground group-hover:translate-x-0.5 group-hover:-translate-y-0.5 transition-all duration-200"
            style={{ color: undefined }}
          />
        </div>

        <p className="text-sm text-muted-foreground leading-relaxed">
          {description}
        </p>

        {/* Tags with project accent color */}
        <div className="flex flex-wrap gap-2 pt-2">
          {project.tags.map((tag) => (
            <span
              key={tag}
              className="text-xs px-2.5 py-1 rounded-full font-medium transition-colors duration-200"
              style={{
                backgroundColor: `${project.accentColor.replace(')', ' / 0.1)')}`,
                color: project.accentColor,
              }}
            >
              {tag}
            </span>
          ))}
        </div>
      </div>
    </motion.a>
  );
}

export function ProjectsSection() {
  return (
    <motion.div
      variants={container}
      initial="hidden"
      animate="show"
      className="grid grid-cols-1 md:grid-cols-2 gap-8"
    >
      {projects.map((project) => (
        <ProjectCard key={project.title} project={project} />
      ))}
    </motion.div>
  );
}
